<template>
  <form @submit.prevent class="register">
    <div v-if="isShowSign">
      <div class="ribbon"></div>
      <div class="login">
        <h1>Sign in</h1>
        <p>This will be an amazing experience</p>

        <div class="input">
          <div class="blockinput">
            <i class="icon-envelope-alt"></i>
            <input v-model="userLog.name" placeholder="Name" />
          </div>
          <div class="blockinput">
            <i class="icon-unlock"></i>
            <input v-model="userLog.password" placeholder="Password" />
          </div>
        </div>
        <button class="register__btn" @click="sign()">
          Войти
        </button>
        <button class="register__btn" @click="showReg()">
          Еще нет аккаунта? Зарегистрироваться
        </button>
      </div>
      <p v-if="isRegOk" style="margin-top: 10px;">
        Вы успешно зарегистрировались
      </p>
      <p v-if="isSign" style="margin-top: 10px;">
        Вы ввели неверные данные
      </p>
    </div>
    <div v-else-if="!isShowSign">
      <div class="ribbon"></div>
      <div class="login">
        <h1>Sign up</h1>
        <p>This will be an amazing experience</p>

        <div class="input">
          <div class="blockinput">
            <i class="icon-envelope-alt"></i>
            <input v-model="user.name" placeholder="Name" />
          </div>
          <div class="blockinput">
            <i class="icon-unlock"></i>
            <input v-model="user.password" placeholder="Password" />
          </div>
        </div>
        <button class="register__btn" @click="addNewUser()">
          Зарегистрироваться
        </button>
        <button class="register__btn" @click="showLogin()">
          Уже есть аккаунт? Войти
        </button>
      </div>
      <p v-if="isOk" style="margin-top: 10px;">
        Такой аккаунт уже существует. Попробуйте друго имя пользователя
      </p>
    </div>
  </form>
</template>

<script>
import store from "@/store/index.js";
export default {
  data() {
    return {
      isOk: false,
      isSign: false,
      isRegOk: false,
      isShowSign: true,

      user: {
        id: Math.random(1, 10000),
        name: "",
        password: "",
      },
      userLog: {
        name: "",
        password: "",
      },
      users: [
        {
          id: 1,
          name: "Vitya",
          password: "12345",
        },
        {
          id: 2,
          name: "Kolya",
          password: "12345",
        },
        {
          id: 3,
          name: "Andrey1",
          password: "12345",
        },
        {
          id: 4,
          name: "Vitya2",
          password: "12345",
        },
        {
          id: 5,
          name: "Kolya3",
          password: "12345",
        },
        {
          id: 6,
          name: "Andrey4",
          password: "12345",
        },
        {
          id: 7,
          name: "Vitya444",
          password: "12345",
        },
        {
          id: 8,
          name: "Kolya222",
          password: "12345",
        },
        {
          id: 9,
          name: "Andrey1212",
          password: "12345",
        },
      ],
    };
  },
  emits: ["enter"],
  methods: {
    addNewUser() {
      let arr = [];
      for (let i = 0; i < this.users.length; i++) {
        arr.push(this.users[i]["name"] === this.user.name);
      }
      if (arr.includes(true)) {
        this.isOk = true;
        this.isRegOk = false;
      } else {
        this.users.push({
          id: this.users[this.users.length - 1]["id"] + 1,
          name: this.user.name,
          password: this.user.password,
        });
        this.isOk = false;
        this.isShowSign = true;
        this.isRegOk = true;
      }

      console.log(this.users);
    },
    showLogin() {
      this.isShowSign = true;
    },
    showReg() {
      this.isShowSign = false;
    },
    sign() {
      if (
        this.users.some(
          (x) =>
            x["name"] === this.userLog.name &&
            x["password"] === this.userLog.password
        )
      ) {
        this.isSign = false;
        this.isRegOk = false;
        this.$router.push("/about");

        store.commit("showName", {
          name: this.userLog.name,
        });
        store.commit("login");
      } else {
        this.isSign = true;
        store.commit("notlogin");
      }
    },
  },
};
</script>

